-size: 4rem;\n  color: var(--main-color);\n}\n\nimg {\n  display: block;\n}\n\n/* form styles */\n\nform input[type='search'] {\n  padding: 5px;\n  border: 1px solid gray;\n  float: left;\n  width: 80%;\n  background-color: #f1f1f1;\n  outline: none;\n  font-size: 1.05rem;\n}\n\nform button {\n  float: left;\n  width: 20%;\n  padding: 5px;\n  background: #2196f3;\n  color: #fff;\n  border: 1px solid gray;\n  border-left: none;\n  cursor: pointer;\n  font-size: 1.05rem;\n}\n\n/* Temps radio buttons */\n\n.temps {\n  background-color: #efefef;\n  border-radius: 4px;\n  border: 1px solid #d0d0d0;\n  display: inline-flex;\n  justify-content: center;\n  align-items: center;\n}\n.temps label {\n  width: 6rem;\n  cursor: pointer;\n}\n.temps label span {\n  text-align: center;\n  padding: 3px 0px;\n  display: flex;\n  justify-content: center;\n}\n.temps label input {\n  display: none;\n}\n.temps input:checked + span {\n  background-color: #404040;\n  color: #f7f7f7;\n}\n\n/* output styles */\n.single-weather {\n  display: flex;\n  gap: 30px;\n  background: var(--main-bg);\n  border-radius: 10px;\n  padding: 10px;\n  box-shadow: rgba(0, 0, 0, 0.4) 0px 3px 9px;\n}\n\n.single-weather h2 {\n  color: var(--main-color);\n}\n\n.single-weather p {\n  font-size: 0.8rem;\n}\n\n.city-details {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 5px;\n}\n\n.city-details img {\n  width: 150px;\n}\n\n.weather-details {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n/*  cards styles */\n\n.cards {\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n  justify-content: center;\n  gap: 20px;\n}\n.card {\n  flex-basis: 1;\n  background-color: var(--main-bg);\n  padding: 10px;\n  border-radius: 5px;\n  box-shadow: rgba(0, 0, 0, 0.4) 0px 3px 9px;\n}\n\n.card * {\n  text-align: center;\n  margin-top: 3px;\n}\n\n.card h3 {\n  color: var(--main-color);\n}\n\nfooter a {\n  color: var(--main-color);\n  text-decoration: none;\n}\n\n/* spinner styles */\n#spinner:not([hidden]) {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#spinner::after {\n  content: '';\n  width: 50px;\n  height: 50px;\n  border: 5px solid #f3f3f3;\n  border-top: 8px solid var(--main-color);\n  border-radius: 100%;\n  will-change: transform;\n  animation: spin 1s infinite linear;\n}\n\n@keyframes spin {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n.error {\n  color: #c03d1d;\n  font-size: 0.8rem;\n}\n"],"sourceRoot":""}]);
